import type { Teams } from '@/types';
import userSvg from '@/assets/images/user.svg';

export function getIndexByNumber(number: string, teams: Teams[]) {
  return teams.findIndex((element) =>
    element.assignedNumbers.find((e) => e.number === number)
  );
}

export function getUserByIndex(index: number, teams: Teams[]) {
  if (index !== -1) return teams[index];
  return null;
}

export const defaultImage = userSvg;
